@import "vars";

/*======================================================*/
/*FONTS*/
/*======================================================*/
@import "fonts";

/*======================================================*/
/*NORMALIZE AND RESET*/
/*======================================================*/
@import "../libs/normalize-css/normalize";
@import "my-reset";

/*======================================================*/
/*TYPOGRAPHY*/
/*======================================================*/
@import "typography";

/*======================================================*/
/*CUSTOM*/
/*======================================================*/

/*MAIN BLOCKS*/
.wrapper {
	margin: 0 auto;
	//min-height: 100%
	//height: auto !important
	//height: 100%
	min-height: 100vh;
	overflow: hidden;
	position: relative;

	&:before {
		content: '';
		position: absolute;
		left: $layout-width;
		top: 0;
		bottom: 0;
		width: 40px;
		margin-left: -40px;
		box-shadow: 5px 0 25px rgba(0, 0, 0, 0.15); } }

.cross {
	position: relative;
	min-height: 100vh;
	padding-top: $header-height;

	@media only screen and (max-width: $mq-desk-md) {
		padding-top: $header-height-mq-md; }

	@media only screen and (max-width: $mq-desk-sm) {
		padding-top: $header-height-mq-sm;

		.entry ~ .page & {
			min-height: calc(100vh - #{$entry-height-mq}); } } }

.main {
	padding: 48px 0 0;
	position: relative;
	clear: both;
	z-index: 4;

	@media only screen and (max-width: $mq-desk-md) {
		padding-top: 40px; } }

.main__holder {
	width: 100%;
	@include clear; }

.header {
	height: $header-height;
	position: absolute;
	top: 0;
	width: 100%;
	padding-top: 30px;
	background-color: #fff;
	box-shadow: 0 5px 25px transparent;
	transition-property: box-shadow, border-color, height, padding-top;
	transition-duration: $animation-speed;
	z-index: 5;

	.home-page & {
		border-bottom-color: transparent;

		.header-fixed & {
			width: calc(100% - #{$entry-width-home-page}); } }

	.header-fixed & {
		position: fixed;
		width: calc(100% - #{$entry-width});
		max-width: $layout-width - $entry-max-width;
		box-shadow: 0 5px 25px rgba(0, 0, 0, 0.15);
		border-color: transparent; }


	.header-transform & {
		height: $header-height-small;
		padding-top: 0; } }

.content-wrap {
	float: left;
	width: 100%; }

.content {
	margin: 0 0 $bottom-space*2 $sidebar-left-width;
	padding-left: $layout-grid-space;
	padding-right: $layout-grid-space;
	min-height: 50px;

	.home-page & {
		margin-left: 0; } }

.sidebar {
	float: left;
	margin: 0 0 0 -100%;
	width: $sidebar-left-width;
	padding-left: $layout-grid-space;

	.home-page & {
		display: none; } }

.footer__holder {
	left: $sidebar-left-width;
	width: calc(100% - #{$sidebar-left-width});

	.home-page & {
		left: 0;
		width: 100%; } }

.layout {
	//max-width: $layout-width
	padding: 0 $layout-space-horizontal; }

.layout-inside {
	margin-left: $layout-grid-space;
	margin-right: $layout-grid-space; }

%text-layout-space {
	padding: {
		left: $text-layout-space-horizontal;
		right: $text-layout-space-horizontal; } }

.text-layout {
	@extend %text-layout-space;
	max-width: $text-layout-width;
	margin-left: auto;
	margin-right: auto; }

@media only screen and (max-width: $mq-desk-md) {
	// layout
	$layout-grid-space: 50px;

	// sidebars
	$sidebar-left-width: 250px;

	.header {
		height: $header-height-mq-md;
		padding-top: 3px; }

	.header-fixed {
		.header {
			width: calc(100% - #{$entry-width-mq}); }

		.home-page .header {
			width: calc(100% - #{$entry-width-home-page-mq}); } }

	.content {
		margin-left: $sidebar-left-width;
		padding-left: $layout-grid-space;
		padding-right: $layout-grid-space; }

	.sidebar {
		width: $sidebar-left-width;
		padding-left: $layout-grid-space; }

	.footer__holder {
		left: $sidebar-left-width;
		width: calc(100% - #{$sidebar-left-width}); }

	.layout-inside {
		margin-left: $layout-grid-space;
		margin-right: $layout-grid-space; } }

@media only screen and (max-width: $mq-desk-sm) {
	.header {
		height: $header-height-mq-sm;
		padding-top: 0;
		width: 100%;

		.home-page & {
			border-bottom-color: $border-color; }

		.header-fixed &, .header-fixed .home-page & {
			width: 100%;
			top: $entry-height-mq-small;
			max-width: none;
			min-width: $layout-min-width; }

		.header-transform & {
			height: $header-height-mq-sm; } } }


/*MAIN BLOCKS end*/

/*WIDGETS*/
@import "sliders";
@import "forms";

/*object fit fixed for ie9*/
.object-fit-bg {
	background: no-repeat center center;

	&--contain {
		background-size: contain; }

	&--cover {
		background-size: cover; }

	img {
		opacity: 0;
		visibility: hidden;
		display: none; } }

/*object fit fixed for ie9 end*/
/*WIDGETS end*/

/*ENTRY*/
.entry-spacer {
	width: $entry-width;
	max-width: $entry-max-width;
	float: left;
	max-height: 100vh;
	height: 50px;
	transition-property: width;
	transition-duration: $animation-speed;

	.home-page & {
		width: $entry-width-home-page;
		max-width: $entry-max-width-home; }

	@media only screen and (max-width: $mq-desk-md) {
		width: $entry-width-mq;

		.home-page & {
			width: $entry-width-home-page-mq; } }

	@media only screen and (max-width: $mq-desk-sm) {
		display: none; } }

.entry-small-bg {
	display: none;

	@media only screen and (max-width: $mq-desk-sm) {
		display: block;
		position: fixed;
		left: 0;
		top: 0;
		height: $entry-height-mq-small;
		width: 100%;
		min-width: $layout-min-width;
		opacity: 0;
		visibility: hidden;
		@include linear-gradient(to right, #019875, #9800f9);
		z-index: 9;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: url(../img/frame-mask.png);
			z-index: -1; }

		.header-fixed & {
			opacity: 1;
			visibility: visible; } } }

.entry {
	width: $entry-width;
	max-width: $entry-max-width;
	position: fixed;
	height: 100vh;
	overflow-x: hidden;
	overflow-y: hidden;
	color: #fff;
	transition-property: width;
	transition-duration: $animation-speed;

	.home-page & {
		width: $entry-width-home-page;
		max-width: $entry-max-width-home; }

	a {
		text-decoration: none;
		color: #fff; }

	&__holder {
		position: relative;
		min-height: 100vh;
		z-index: 1;
		@include linear-gradient(to right, #019875 0%, #108982 44%, #2e6b9c 100%);

		.home-page & {
			@include linear-gradient(to right, #019875, #4c4db6); }

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: url(../img/frame-mask.png);
			z-index: -1; } }

	&__img {
		position: absolute;
		left: 125px;
		bottom: 0;
		max-height: 80%;
		min-width: 400px;
		width: 75%;
		overflow: hidden;
		transition-property: width, height;
		transition-duration: $animation-speed;

		&:before {
			content: '';
			display: block;
			height: 0;
			padding-top: 143%; }

		img {
			position: absolute;
			left: 0;
			top: 0;
			height: 100%;
			width: 100%;
			object-fit: contain; }

		&-inner {
			position: absolute;
			height: 100%;
			width: 100%;
			left: 0;
			top: 0;

			@media only screen and (min-width: #{$mq-desk-sm+1}) {
				transform: matrix(1, 0, 0, 1, 0, 0) !important; } } } // for js

	&__frame {
		position: absolute;
		left: 100px;
		right: 100px;
		top: 62px;
		bottom: 62px;
		//left: 15.6%
		//right: 15.6%
		//bottom: 6.3%
		//top: 6.3%
		z-index: 1; }

	&__logo {
		font-family: $font-alt;
		font-weight: 700;
		font-size: 3.0rem;
		line-height: 1.2;
		margin-bottom: 1.4em;

		@media only screen and (min-width: #{$mq-desk-sm+1}) {
			transform: matrix(1, 0, 0, 1, 0, 0) !important; } } // for js

	&__title {
		margin-bottom: 2.2em;

		.home-page & {
			max-width: 50%; }

		@media only screen and (min-width: #{$mq-desk-sm+1}) {
			transform: matrix(1, 0, 0, 1, 0, 0) !important; } } // for js

	&__message {
		font-size: 1.4rem;
		line-height: 1.5;
		@include font-bold;
		text-transform: uppercase;
		letter-spacing: 0.1em;
		margin-bottom: 2em;

		.home-page & {
			position: absolute;
			right: 0;
			top: 14px; }

		a {
			color: #fff;

			span {
				display: inline-block;
				line-height: 1.2;
				border-bottom: 2px solid #fff;
				transition-property: border-color;
				transition-duration: $animation-speed; }

			.no-touchevents &:hover span {
				border-bottom-color: transparent; } } }

	&__copyright {
		position: absolute;
		bottom: 0;
		font-size: 1.6rem;
		line-height: 1.875;

		.nobr {
			display: block; } }

	&__contacts {
		position: absolute;
		bottom: 0;
		right: 0;
		font-size: 1.6rem;
		line-height: 1.875;
		text-align: right;
		z-index: 1; }

	@media only screen and (max-width: $mq-desk-lg) {
		&__copyright {
			bottom: 50px; }

		&__contacts {
			right: auto;
			left: 0; } }

	@media only screen and (max-width: $mq-desk-md) {
		width: $entry-width-mq;
		font-size: 1.6rem;

		.home-page & {
			width: $entry-width-home-page-mq; }

		&__img {
			left: 35px;
			width: 85%; }

		&__frame {
			left: 9.4%;
			right: 9.4%;
			bottom: 43px;
			top: 43px; }

		.home-page &__title {
			max-width: none; }

		&__message, .home-page &__message {
			position: relative;
			right: auto; } }

	@media only screen and (max-width: $mq-desk-sm) {
		&, .home-page & {
			position: relative;
			width: 100%;
			max-width: none;
			height: $entry-height-mq;
			z-index: 10; }

		&__holder, .home-page &__holder {
			min-height: $entry-height-mq;
			overflow-y: hidden;
			@include linear-gradient(to right, #019875, #9800f9);

			&:before {
				background-attachment: fixed; } }

		&__img {
			max-height: none;
			width: 320px;
			min-width: 330px;
			bottom: auto;
			top: 48px;
			left: 50%;
			transform: translate(-50%, 0) !important; }

		&__frame {
			left: 50px;
			right: 50px;
			bottom: 45px;
			top: 45px; }

		&__title, .home-page &__title {
			position: absolute;
			bottom: 0;
			margin: 0;
			max-width: 280px; }

		&__message, .home-page &__message {
			position: fixed;
			left: 0;
			width: 100%;
			text-align: right;
			min-width: $layout-min-width;
			padding-right: 0 + 50px;
			top: 14px + 45px;
			font-size: 1.4rem;
			will-change: transform;
			z-index: 10; }

		&__logo, .home-page &__logo {
			position: fixed;
			left: 0 + 50px;
			top: 4px + 45px;
			will-change: transform;
			z-index: 15; }

		&__copyright {
			display: none; }

		&__contacts {
			right: 0;
			left: auto; } } }

.page {
	position: relative;
	background-color: #fff;
	max-width: $layout-width - $entry-max-width;

	.home-page & {
		max-width: $layout-width - $entry-max-width-home; }

	@media only screen and (max-width: $mq-desk-lg) {
		&, .home-page & {
			max-width: none; } }

	.entry + & {
		overflow: hidden; } }

/*ENTRY end*/

/*HEADER*/
.header {
	border-bottom: 1px solid $border-color;

	.layout-inside {
		height: 100%; }

	&__holder {
		position: relative;
		height: 100%;
		@include clear; } }

/*main navigation*/
.nav {
	//padding: 35px 30px 35px 0
	display: table;
	height: 100%;
	width: 100%;

	&__list {
		display: table-cell;
		vertical-align: middle;
		margin: 0;
		padding-right: 66px;
		font-size: 0;
		letter-spacing: -4px;

		li {
			display: inline-block;
			margin-right: 3.0%;
			font-size: 1.4rem;
			line-height: 1.5;
			letter-spacing: normal;

			.home-page & {
				margin-right: 2.0%;
				font-size: 1.3rem; }

			@media only screen and (max-width: $mq-desk-md) {
				margin-right: 2.0%; }

			@media only screen and (max-width: $mq-desk-sm) {
				font-size: 1.3rem;

				a {
					letter-spacing: 0.05em; } } }

		a {
			display: inline-block;
			text-decoration: none;
			vertical-align: top;
			@include font-bold;
			letter-spacing: 0.1em;
			text-transform: uppercase;
			color: $text-color;
			transition-property: color;
			transition-duration: $animation-speed;

			.home-page & {
				letter-spacing: 0.05em; } }

		.no-touchevents & a:hover, .no-touchevents & li.current a, li.current a {
			color: $text-color-alt; } }

	.nav-drop {
		display: none; } }

/*main navigation end*/

/*lang*/
@import "lang";
/*lang end*/

/*search*/
.btn-search {
	position: absolute;
	right: -14px;
	top: 50%;
	width: 40px;
	height: 40px;
	transform: translate(0, -50%);

	span {
		@include hide-text; }

	i {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 12px;
		height: 12px;
		z-index: 2;
		pointer-events: none;
		transform: translate(-50%, -50%);

		&:before, &:after {
			content: '';
			position: absolute;
			background-color: black;
			border: 2px solid black;
			transition-property: background-color, border-color, transform;
			transition-duration: $animation-speed; }

		&:before {
			left: 0;
			top: 0;
			width: 10px;
			height: 10px;
			border-radius: 5px;
			background: none; }

		&:after {
			bottom: 0;
			right: 0;
			height: 2px;
			width: 6px;
			border-radius: 2px;
			border: none;
			transform-origin: right bottom;
			transform: rotate(38deg); } }

	.no-touchevents &:hover i {
		&:before {
			border-color: $text-color-alt; }

		&:after {
			background-color: $text-color-alt; } } }

/*search end*/

/*HEADER end*/

/*MAIN*/
/*breadcrumbs*/
.breadcrumbs {
	font-size: 1.6rem;
	color: $text-color-light;
	margin-bottom: $bottom-space;

	$bc-color-link: black;

	&__list, .#{$user-content} &__list {
		padding: 0;
		margin: 0;

		li {
			padding: 0 17px 0 0;
			margin: 0;

			&:before, &:first-child:before {
				content: none; } } }

	&__list {
		cursor: default;
		overflow: hidden;

		li {
			display: inline-block;
			vertical-align: top;
			position: relative;

			&:after {
				content: '/';
				position: absolute;
				right: 10px;
				top: 1px;
				height: 0;
				width: 0;
				line-height: 1.2;
				border: none;
				color: $text-color-alt; }

			&:last-child:after {
				content: none; }

			span {
				display: inline-block;
				vertical-align: top;
				line-height: 1.2;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
				max-width: 260px; } }

		a {
			display: inline-block;
			vertical-align: top;
			color: $bc-color-link;
			text-decoration: none;
			cursor: pointer;

			.no-touchevents &:hover {
				text-decoration: none; }

			span {
				border-bottom: 1px solid lighten($bc-color-link, 70%);
				transition-property: border-color, color;
				transition-duration: $animation-speed; }

			.no-touchevents &:hover span {
				border-color: transparent;
				color: $text-color-alt; } } } }

/*breadcrumbs end*/

/*years list*/
.years {
	margin-bottom: $bottom-space;
	border: 1px solid $border-color;
	padding: 0 55px;
	position: relative;

	&__list, .#{$user-content} &__list {
		margin: 0;

		li {
			padding: 19px 0;
			margin: 0;

			&:before {
				content: none; } } }

	&__list {
		li {
			display: inline-block;
			vertical-align: top;

			&.swiper-slide {
				width: auto; } }

		a {
			display: inline-block;
			vertical-align: top;
			padding: 5px 15px;
			text-decoration: none;
			color: $text-color;
			font-size: 1.4rem;
			line-height: 1.4;
			@include font-bold;
			text-transform: uppercase;
			letter-spacing: 0.1em;
			transition-property: color;
			transition-duration: $animation-speed; }

		.no-touchevents & a:hover, .no-touchevents & li.current a, li.current a {
			color: $text-color-alt; } }

	.swiper-container {
		position: relative;

		&:before, &:after {
			content: '';
			position: absolute;
			top: 0;
			height: 100%;
			width: 15px;
			z-index: 2;
			pointer-events: none; }

		&:before {
			left: 0;
			@include linear-gradient(to right, white 0%, rgba(255, 255, 255, 0.7) 50%, rgba(255, 255, 255, 0.1) 100%); }

		&:after {
			right: 0;
			@include linear-gradient(to left, white 0%, rgba(255, 255, 255, 0.7) 50%, rgba(255, 255, 255, 0.1) 100%); } }

	.swiper-button {
		&-prev {
			left: 5px; }

		&-next {
			right: 5px; } } }

/*years list end*/

/*gallery*/
.gallery {
	margin-bottom: $bottom-space;

	&__visual {
		margin-bottom: 2rem;

		.swiper-slide {

			&__inner {
				display: block;
				text-decoration: none;
				color: $text-color;
				position: relative;
				background-color: lightgray;

				&:before {
					content: '';
					display: block;
					height: 0;
					//padding-top: 62.5%
					padding-top: 66.6%; } }

			img {
				//width: auto
				height: auto;
				max-width: 100%;
				max-height: 100%;
				transform: translate(-50%,-50%);
				position: absolute;
				left: 50%;
				top: 50%;
				object-fit: contain;
				width: 100%; } } }


	&__caption {
		padding-left: 32px;
		position: relative;
		background-color: #fff;
		font-size: 1.6rem;
		line-height: 1.5;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 6px;
			bottom: 5px;
			border-right: 2px solid $text-color-alt; }

		> *:last-child, & .swiper-slide__inner > *:last-child {
			margin-bottom: 0;
			padding-bottom: 0; } } }

/*gallery end*/

/*quotes*/
.quotes {
	margin-bottom: $bottom-space;

	.swiper-container {
		margin-bottom: $bottom-space*2; }

	.swiper-slide {
		background-color: #fff; }

	.swiper-slide__inner {

		h3 {
			font-family: $font-alt;
			font-weight: bold;
			font-size: 4.8rem;
			line-height: 1.2; }

		& > *:last-child {
			margin-bottom: 0;
			padding-bottom: 0; } } }

/*quotes end*/

/*dates*/
.dates {
	margin-bottom: $bottom-space;

	.dates__list li {
		margin: $bottom-space 0; }

	&__title {
		@include font-bold;
		font-size: 1.6rem;
		margin-bottom: 0.3em; }

	&__text > *:last-child {
		margin-bottom: 0; } }

/*dates end*/
/*MAIN end*/

/*SIDEBAR*/
.sidebar {
	&__holder {
		position: relative; } }

/*menu*/
.menu {
	$m-space: 20px;

	margin: -#{$m-space};
	font-size: 1.6rem;
	line-height: 1.5;

	&__list {

		li {
			padding-bottom: 1px;

			.no-touchevents &.current, &.current {
				a {
					color: transparent;
					transition-delay: $animation-speed - 0.07s; } }

			.no-touchevents &.current, &.current {
				a {
					color: white;

					&:before {
						opacity: 1;
						visibility: visible;
						transform: scale(1); } } } }

		a {
			display: block;
			position: relative;
			padding: 16px round($m-space);
			text-decoration: none;
			color: $text-color;
			transition-property: color;
			transition-duration: $animation-speed;
			z-index: 1;

			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				width: 100%;
				pointer-events: none;
				opacity: 0;
				visibility: hidden;
				transform: scale(0.8);
				transition-property: opacity, visibility, transform;
				transition-duration: $animation-speed;
				transition-timing-function: ease-in-out;
				@include linear-gradient(to right, #019875, #9800f9);
				z-index: -1; }

			.no-touchevents &:hover {
				color: $text-color-alt; } }

		ul {
			position: relative;

			a {
				padding-left: round($m-space*2.5);
				color: $text-color-light; } } }

	@media only screen and (max-width: $mq-desk-md) {
		& {
			$m-space: 15px;
			margin: -#{$m-space};

			&__list {
				a {
					padding: 14px round($m-space); }

				ul a {
					padding-left: round($m-space*2.5); } } } } }

/*menu end*/

/*SIDEBAR end*/

/*FOOTER*/
.footer {
	height: 0;
	position: relative;
	clear: both;
	z-index: 100;
	font-size: 1.6rem;
	line-height: 1.875;
	color: $text-color;

	.layout-inside {
		position: relative; }

	&__holder {
		position: absolute;
		overflow: hidden;
		cursor: default;
		bottom: 0;
		top: auto;
		right: auto;
		padding: 48px 0;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			height: 2px;
			width: 100%;
			pointer-events: none;
			@include linear-gradient(to right, #019875, #9800f9); } } }

/*footer bottom*/
.footer-bottom {
	color: $text-color;

	&__holder {
		position: relative;
		text-align: justify;
		overflow: hidden;
		cursor: default;

		font-size: 1px;
		line-height: 0;
		letter-spacing: -4px;

		&:after {
			content: "1";
			margin-left: 100%;
			height: 1px;
			overflow: hidden;
			display: inline-block; } }

	&__col {
		display: inline-block;
		vertical-align: top;
		font-size: 1.6rem;
		line-height: 1.875;
		letter-spacing: normal;

		&:last-child {
			text-align: right; } }

	a {
		color: $text-color;
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: underline; } }

	&__contacts {
		font-size: 0;
		letter-spacing: -4px;

		&.hide {
			display: none; }

		li {
			display: inline-block;
			vertical-align: top;
			position: relative;
			font-size: 1.6rem;
			line-height: 1.875;
			letter-spacing: normal;
			padding-right: 20px;

			&:after {
				content: '/';
				position: absolute;
				right: 13px;
				top: 0;
				height: 0;
				width: 0;
				border: none;
				color: $text-color-alt; }

			&:last-child:after {
				content: none; } } } }

/*footer bottom end*/

/*footer side*/
.footer-side {
	display: none; }

/*footer side end*/

/*developer*/
.developer {
	display: inline-block;
	padding-right: 4px;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } }

	&__logo {
		display: inline-block;
		vertical-align: top;
		position: relative;
		top: 8px;
		width: 154px;
		height: 20px;
		text-align: left;
		text-indent: -9999px;
		padding-right: 3px;

		&:before, &:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 149px;
			height: 16px;
			transition-property: opacity, transform, filter;
			-webkit-transition-duration: 0.2s;
			transition-duration: 0.2s; }

		&:before {
			content: '';
			position: absolute;
			left: 4px;
			top: 4px;
			background: url('data:image/png;base64,');
			background-size: 2px; }

		&:after {
			content: '';
			position: absolute;
			background: #00ef85 url('data:image/png;base64,') no-repeat center center;
			background-size: auto 16px; } }

	.no-touchevents a:hover &__logo:before {
		transform: translate(0, 1px);
		filter: blur(2px);
		opacity: 0.6; }

	.no-touchevents a:hover &__logo:after {
		transform: translate(0, -1px); } }

/*developer end*/

@media only screen and (max-width: $mq-desk-lg) {
	.footer-bottom {
		&__col {
			max-width: 73.9%;

			&:last-child {
				max-width: 24.9%; } } } }

@media only screen and (max-width: $mq-desk-sm) {
	.footer-bottom {
		&__col {
			max-width: 59.9%;

			&:last-child {
				max-width: 39.9%; } } } }

@media only screen and (max-width: $mq-desk-sm) {
	// layout
	$layout-grid-space: 50px;

	// sidebars
	$sidebar-left-width: 250px;

	.footer {
		height: auto;

		&__holder {
			left: auto;
			width: 100%;
			padding: 40px 0;

			&:after {
				content: '';
				display: block;
				clear: both; } } }

	.footer-bottom {
		position: relative;
		float: left;
		width: 100%;

		&__holder {
			padding-left: $sidebar-left-width; }

		&__contacts.hide {
			display: block; } }

	.footer-side {
		display: block;
		float: left;
		width: $sidebar-left-width;
		margin: 0 0 0 -100%;
		position: relative;
		padding-right: $layout-grid-space;

		.copyright .nobr {
			display: block; } } }

/*FOOTER end*/

