/*TYPOGRAPHY*/
/*======================================================*/
html {
	font-size: 62.5%;
}

body {
	font-family: "Open Sans", sans-serif;
	font-weight: 300;
	font-size: 1.8rem;
	line-height: 1.66667;
	height: 100%;
	color: black;
	min-width: 768px;
	background: white url(../img/empty.png) no-repeat fixed;
}

h1, h2, h3, h4, h5, h6 {
	margin: 0;
	padding: 0;
	font-family: "Open Sans", sans-serif;
	font-weight: 300;
	color: black;
}

h1 {
	font-size: 3.6rem;
	line-height: 1.4;
	margin-bottom: 4.5rem;
}

h2 {
	font-size: 3.2rem;
	line-height: 1.2;
	margin-bottom: 3rem;
}

h3 {
	font-size: 2.6rem;
	line-height: 1.2;
	margin-bottom: 3rem;
}

h4 {
	font-weight: 700;
	font-size: 2.0rem;
	margin-bottom: 3rem;
}

h5 {
	font-weight: 700;
	font-size: 1.4rem;
	line-height: 1.4;
	text-transform: uppercase;
	margin-bottom: 3rem;
}

h6 {
	font-size: 1.8rem;
	line-height: 1.66667;
	margin-bottom: 3rem;
}

p {
	padding: 0;
	margin: 0 0 3rem;
}

hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid #d9d9d9;
	margin-bottom: 3rem;
}

a {
	color: #019875;
	text-decoration: underline;
	cursor: default;
	outline: none;
}

a:link {
	cursor: pointer;
}

.no-touchevents a:hover {
	text-decoration: none;
}

a[href^="tel"], .no-touchevents a[href^="tel"]:hover {
	text-decoration: none !important;
	color: inherit;
	cursor: default;
}

a[href^="javascript:"] {
	cursor: default;
}

img {
	border: none;
	vertical-align: top;
	max-width: 100%;
}

table img {
	max-width: none;
}

b, strong, th {
	font-weight: 700;
}

i, em {
	font-weight: 300;
	font-style: italic;
}

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	font-weight: 700;
	font-style: italic;
}

.mark {
	color: tomato;
	font-style: inherit;
	font-weight: inherit;
}

.text-color-alt {
	color: #019875;
}

.align-center {
	text-align: center;
}

.align-left {
	text-align: left;
}

.align-right {
	text-align: right;
}

.float-left {
	float: left;
}

.float-right {
	float: right;
}

.nobr {
	white-space: nowrap;
}

.user-container {
	margin-bottom: 3rem;
}

.user-container > *:last-child {
	margin-bottom: 0;
}

/*btn*/
[class|="btn"], [class*=" btn-"], input[type="submit"], input[type="button"], input[type="reset"], button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	text-align: center;
	text-decoration: none;
	box-sizing: border-box;
	cursor: pointer;
}

[class|="btn"] input[type="submit"], [class|="btn"] input[type="button"], [class|="btn"] input[type="reset"], [class*=" btn-"] input[type="submit"], [class*=" btn-"] input[type="button"], [class*=" btn-"] input[type="reset"] {
	background: url("../img/empty.png") repeat;
	display: block;
	height: 100%;
	padding: 0;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 99;
	border: 0;
	opacity: 0;
}

/*btn-default*/
[class*="btn-default"] {
	position: relative;
	font-family: "Open Sans", sans-serif;
	font-weight: 300;
	font-size: 1.6rem;
	line-height: 24px;
	color: white;
	padding: 12px 25px;
	border: 1px solid transparent;
	background: #019875;
	box-shadow: none;
	-webkit-transition-property: background-color;
	transition-property: background-color;
	-webkit-transition-duration: 0.2s;
	transition-duration: 0.2s;
}

.no-touchevents [class*="btn-default"]:hover {
	background-color: #01654e;
}

[class*="btn-default"]:active {
	-webkit-transform: scale(0.95);
	-ms-transform: scale(0.95);
	transform: scale(0.95);
}

/*btn-default end*/
/*btn-default-alt*/
.btn-default-alt {
	color: #019875;
	border: 2px solid #019875;
	background: white;
}

.no-touchevents .btn-default-alt:hover {
	background: #01654e;
	color: white;
}

/*btn-default-alt end*/
/*btn end*/
.content p + h2, .content ul + h2, .content ol + h2 {
	padding-top: 10px;
}

.content p + h3, .content ul + h3, .content ol + h3 {
	padding-top: 10px;
}

.content p + h4, .content ul + h4, .content ol + h4 {
	padding-top: 10px;
}

.content p + h5, .content ul + h5, .content ol + h5 {
	padding-top: 10px;
}

/*list*/
.content ul, .content ol {
	margin: 3rem 0;
	padding: 0;
	list-style: none;
}

.content ul li, .content ol li {
	padding-left: 30px;
	margin: 1.5rem 0;
	position: relative;
}

.content ul > li:before {
	content: '';
	position: absolute;
	left: 0;
	top: 0.6em;
	height: 0;
	width: 0;
	border: solid transparent;
	border-width: 5px 9px;
	border-left-color: #019875;
	box-sizing: border-box;
}

.content ol {
	counter-reset: section;
}

.content ol > li {
	counter-increment: section;
	height: 1%;
	overflow: hidden;
}

.content ol > li:before {
	position: relative;
	display: inline-block;
	text-align: left;
	font-family: "Noto Serif", serif;
	font-weight: bold;
	color: #019875;
	content: counter(section);
	padding: 0 4px 0 0;
	min-width: 30px;
	margin-left: -30px;
	box-sizing: border-box;
}

.content ul ul,
.content ul ol,
.content ol ul {
	margin: 1.5rem 0;
}

.content .list-reset {
	list-style: none;
	margin: 0;
}

.content .list-reset li, .content .list-reset li:first-child {
	padding: 0;
}

.content .list-reset li:before, .content .list-reset li:first-child:before, .content .list-reset li:first-child:before, .content .list-reset li:first-child:first-child:before {
	content: none;
}

/*list end*/
/*table*/
.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin: 0 0 3rem;
}

.table-auto table {
	margin-bottom: 0;
	width: auto;
	min-width: 100%;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
	width: auto;
}

table td, table th {
	font-size: 1.4rem;
	line-height: 1.3;
	color: #262626;
	padding: 8px 10px;
	box-sizing: border-box;
	border-color: black;
}

.content table {
	margin-bottom: 3rem;
}

.content table.table-list td {
	font-size: 1.8rem;
	line-height: 1.66667;
	padding: 0;
}

.content table.table-list td [style*='justify'] {
	text-align: left !important;
}

.content .table-auto table {
	margin-bottom: 0;
}

.content .tbl {
	border: none;
}

.content .tbl td, .content .tbl th {
	color: #262626;
	background-color: white;
	border: 0 solid #019875;
	font-size: 1.4rem;
	line-height: 1.2;
}

.content .tbl td:first-child, .content .tbl td:first-child, .content .tbl th:first-child, .content .tbl th:first-child {
	border-left-width: 1px;
}

.content .tbl td:last-child, .content .tbl td:last-child, .content .tbl th:last-child, .content .tbl th:last-child {
	border-right-width: 1px;
}

.content .tbl td {
	border-top-width: 1px;
}

.content .tbl tbody tr:first-child td {
	border-top-width: 0;
}

.content .tbl tbody tr:nth-child(even) td {
	background: #019875;
	color: white;
}

.content .tbl th, .content .tbl thead td {
	font-weight: 700;
	font-size: 1.6rem;
	text-align: left;
}

.content .tbl th, .content .tbl thead td, .content .tbl tfoot td {
	background: #019875;
	color: white;
	border-color: #019875;
	border-top-width: 0;
}

/*table end*/
/*blockquote*/
.content .blockquote {
	padding: 0 0 30px;
}

.content blockquote {
	display: block;
	margin: 0;
	padding: 2rem 3rem;
}

.content blockquote q {
	display: block;
	margin: 0;
	padding: 0 0 10px;
	font-family: "Noto Serif", serif;
	font-weight: bold;
	font-size: 2rem;
	line-height: 1.66667;
	color: black;
}

.content blockquote q:before, .content blockquote q:after {
	content: '';
	display: block;
	font-size: 2.4rem;
	line-height: 1.2;
}

.content blockquote q:before {
	content: '«';
	padding-left: 17%;
	padding-bottom: 10px;
}

.content blockquote q:after {
	content: '»';
	text-align: right;
	padding-right: 18.5%;
	padding-top: 8px;
}

.content blockquote cite {
	text-align: right;
	display: block;
	margin-left: auto;
	font-weight: 300;
	font-style: italic;
	font-size: 1.8rem;
	line-height: 1.66667;
	color: black;
}

/*blockquote end*/
/*content img*/
.imgc {
	text-align: center;
	padding-bottom: 3rem;
	clear: both;
}

.imgl {
	text-align: left;
	float: left;
	margin: 0 3rem 3rem 0;
