	content: '/';
	position: absolute;
	right: 10px;
	top: 1px;
	height: 0;
	width: 0;
	line-height: 1.2;
	border: none;
	color: #019875;
}

.breadcrumbs__list li:last-child:after {
	content: none;
}

.breadcrumbs__list li span {
	display: inline-block;
	vertical-align: top;
	line-height: 1.2;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	max-width: 260px;
}

.breadcrumbs__list a {
	display: inline-block;
	vertical-align: top;
	color: black;
	text-decoration: none;
	cursor: pointer;
}

.no-touchevents .breadcrumbs__list a:hover {
	text-decoration: none;
}

.breadcrumbs__list a span {
	border-bottom: 1px solid #b3b3b3;
	transition-property: border-color, color;
	transition-duration: 0.2s;
}

.no-touchevents .breadcrumbs__list a:hover span {
	border-color: transparent;
	color: #019875;
}

/*breadcrumbs end*/
/*years list*/
.years {
	margin-bottom: 3rem;
	border: 1px solid #d9d9d9;
	padding: 0 55px;
	position: relative;
}

.years__list, .content .years__list {
	margin: 0;
}

.years__list li, .content .years__list li {
	padding: 19px 0;
	margin: 0;
}

.years__list li:before, .content .years__list li:before {
	content: none;
}

.years__list li {
	display: inline-block;
	vertical-align: top;
}

.years__list li.swiper-slide {
	width: auto;
}

.years__list a {
	display: inline-block;
	vertical-align: top;
	padding: 5px 15px;
	text-decoration: none;
	color: black;
	font-size: 1.4rem;
	line-height: 1.4;
	font-weight: 700;
	text-transform: uppercase;
	letter-spacing: 0.1em;
	transition-property: color;
	transition-duration: 0.2s;
}

.no-touchevents .years__list a:hover, .no-touchevents .years__list li.current a, .years__list li.current a {
	color: #019875;
}

.years .swiper-container {
	position: relative;
}

.years .swiper-container:before, .years .swiper-container:after {
	content: '';
	position: absolute;
	top: 0;
	height: 100%;
	width: 15px;
	z-index: 2;
	pointer-events: none;
}

.years .swiper-container:before {
	left: 0;
	background: white;
	background: -webkit-linear-gradient(left, white 0%, rgba(255, 255, 255, 0.7) 50%, rgba(255, 255, 255, 0.1) 100%);
	background: linear-gradient(to right, white 0%, rgba(255, 255, 255, 0.7) 50%, rgba(255, 255, 255, 0.1) 100%);
}

.years .swiper-container:after {
	right: 0;
	background: white;
	background: -webkit-linear-gradient(right, white 0%, rgba(255, 255, 255, 0.7) 50%, rgba(255, 255, 255, 0.1) 100%);
	background: linear-gradient(to left, white 0%, rgba(255, 255, 255, 0.7) 50%, rgba(255, 255, 255, 0.1) 100%);
}

.years .swiper-button-prev {
	left: 5px;
}

.years .swiper-button-next {
	right: 5px;
}

/*years list end*/
/*gallery*/
.gallery {
	margin-bottom: 3rem;
}

.gallery__visual {
	margin-bottom: 2rem;
}

.gallery__visual .swiper-slide__inner {
	display: block;
	text-decoration: none;
	color: black;
	position: relative;
	background-color: lightgray;
}

.gallery__visual .swiper-slide__inner:before {
	content: '';
	display: block;
	height: 0;
	padding-top: 66.6%;
}

.gallery__visual .swiper-slide img {
	height: auto;
	max-width: 100%;
	max-height: 100%;
	transform: translate(-50%, -50%);
	position: absolute;
	left: 50%;
	top: 50%;
	object-fit: contain;
	width: 100%;
}

.gallery__caption {
	padding-left: 32px;
	position: relative;
	background-color: #fff;
	font-size: 1.6rem;
	line-height: 1.5;
}

.gallery__caption:before {
	content: '';
	position: absolute;
	left: 0;
	top: 6px;
	bottom: 5px;
	border-right: 2px solid #019875;
}

.gallery__caption > *:last-child, .gallery__caption .swiper-slide__inner > *:last-child {
	margin-bottom: 0;
	padding-bottom: 0;
}

/*gallery end*/
/*quotes*/
.quotes {
	margin-bottom: 3rem;
}

.quotes .swiper-container {
	margin-bottom: 6rem;
}

.quotes .swiper-slide {
	background-color: #fff;
}

.quotes .swiper-slide__inner h3 {
	font-family: "Noto Serif", serif;
	font-weight: bold;
	font-size: 4.8rem;
	line-height: 1.2;
}

.quotes .swiper-slide__inner > *:last-child {
	margin-bottom: 0;
	padding-bottom: 0;
}

/*quotes end*/
/*dates*/
.dates {
	margin-bottom: 3rem;
}

.dates .dates__list li {
	margin: 3rem 0;
}

.dates__title {
	font-weight: 700;
	font-size: 1.6rem;
	margin-bottom: 0.3em;
}

.dates__text > *:last-child {
	margin-bottom: 0;
}

/*dates end*/
/*MAIN end*/
/*SIDEBAR*/
.sidebar__holder {
	position: relative;
}

/*menu*/
.menu {
	margin: -20px;
	font-size: 1.6rem;
	line-height: 1.5;
}

.menu__list li {
	padding-bottom: 1px;
}

.no-touchevents .menu__list li.current a, .menu__list li.current a {
	color: transparent;
	transition-delay: 0.13s;
}

.no-touchevents .menu__list li.current a, .menu__list li.current a {
	color: white;
}

.no-touchevents .menu__list li.current a:before, .menu__list li.current a:before {
	opacity: 1;
	visibility: visible;
	transform: scale(1);
}

.menu__list a {
	display: block;
	position: relative;
	padding: 16px 20px;
	text-decoration: none;
	color: black;
	transition-property: color;
	transition-duration: 0.2s;
	z-index: 1;
}

.menu__list a:before {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	pointer-events: none;
	opacity: 0;
	visibility: hidden;
	transform: scale(0.8);
	transition-property: opacity, visibility, transform;
	transition-duration: 0.2s;
	transition-timing-function: ease-in-out;
	background: #019875;
	background: -webkit-linear-gradient(left, #019875, #9800f9);
	background: linear-gradient(to right, #019875, #9800f9);
	z-index: -1;
}

.no-touchevents .menu__list a:hover {
	color: #019875;
}

.menu__list ul {
	position: relative;
}

.menu__list ul a {
	padding-left: 50px;
	color: #808080;
}

@media only screen and (max-width: 1599px) {
	.menu {
		margin: -15px;
	}
	.menu__list a {
		padding: 14px 15px;
	}
	.menu__list ul a {
		padding-left: 38px;
	}
}

/*menu end*/
/*SIDEBAR end*/
/*FOOTER*/
.footer {
	height: 0;
	position: relative;
	clear: both;
	z-index: 100;
	font-size: 1.6rem;
	line-height: 1.875;
	color: black;
}

.footer .layout-inside {
	position: relative;
}

.footer__holder {
	position: absolute;
	overflow: hidden;
	cursor: default;
	bottom: 0;
	top: auto;
	right: auto;
	padding: 48px 0;
}

.footer__holder:before {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	height: 2px;
	width: 100%;
	pointer-events: none;
	background: #019875;
	background: -webkit-linear-gradient(left, #019875, #9800f9);
	background: linear-gradient(to right, #019875, #9800f9);
}

/*footer bottom*/
.footer-bottom {
	color: black;
}

.footer-bottom__holder {
	position: relative;
	text-align: justify;
	overflow: hidden;
	cursor: default;
	font-size: 1px;
	line-height: 0;
	letter-spacing: -4px;
}

.footer-bottom__holder:after {
	content: "1";
	margin-left: 100%;
	height: 1px;
	overflow: hidden;
	display: inline-block;
}

.footer-bottom__col {
	display: inline-block;
	vertical-align: top;
	font-size: 1.6rem;
	line-height: 1.875;
	letter-spacing: normal;
}

.footer-bottom__col:last-child {
	text-align: right;
}

.footer-bottom a {
	color: black;
	text-decoration: none;
}

.no-touchevents .footer-bottom a:hover {
	text-decoration: underline;
}

.footer-bottom__contacts {
	font-size: 0;
	letter-spacing: -4px;
}

.footer-bottom__contacts.hide {
	display: none;
}

.footer-bottom__contacts li {
	display: inline-block;
	vertical-align: top;
	position: relative;
	font-size: 1.6rem;
	line-height: 1.875;
	letter-spacing: normal;
	padding-right: 20px;
}

.footer-bottom__contacts li:after {
	content: '/';
	position: absolute;
	right: 13px;
	top: 0;
	height: 0;
	width: 0;
	border: none;
	color: #019875;
}

.footer-bottom__contacts li:last-child:after {
	content: none;
}

/*footer bottom end*/
/*footer side*/
.footer-side {
	display: none;
}

/*footer side end*/
/*developer*/
.developer {
	display: inline-block;
	padding-right: 4px;
}

.developer a {
	text-decoration: none;
}

.no-touchevents .developer a:hover {
	text-decoration: none;
}

.developer__logo {
	display: inline-block;
	vertical-align: top;
	position: relative;
	top: 8px;
	width: 154px;
	height: 20px;
	text-align: left;
	text-indent: -9999px;
	padding-right: 3px;
}

.developer__logo:before, .developer__logo:after {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 149px;
	height: 16px;
	transition-property: opacity, transform, filter;
	-webkit-transition-duration: 0.2s;
	transition-duration: 0.2s;
}

.developer__logo:before {
	content: '';
	position: absolute;
	left: 4px;
	top: 4px;
	background: url("data:image/png;base64,");
	background-size: 2px;
}

.developer__logo:after {
	content: '';
	position: absolute;
	background: #00ef85 url("data:image/png;base64,") no-repeat center center;
	background-size: auto 16px;
}

.no-touchevents a:hover .developer__logo:before {
	transform: translate(0, 1px);
	filter: blur(2px);
	opacity: 0.6;
}

.no-touchevents a:hover .developer__logo:after {
	transform: translate(0, -1px);
}

/*developer end*/
@media only screen and (max-width: 1919px) {
	.footer-bottom__col {
		max-width: 73.9%;
	}
	.footer-bottom__col:last-child {
		max-width: 24.9%;
	}
}

@media only screen and (max-width: 1199px) {
	.footer-bottom__col {
		max-width: 59.9%;
	}
	.footer-bottom__col:last-child {
		max-width: 39.9%;
	}
}

@media only screen and (max-width: 1199px) {
	.footer {
		height: auto;
	}
	.footer__holder {
		left: auto;
		width: 100%;
		padding: 40px 0;
	}
	.footer__holder:after {
		content: '';
		display: block;
		clear: both;
	}
	.footer-bottom {
		position: relative;
		float: left;
		width: 100%;
	}
	.footer-bottom__holder {
		padding-left: 250px;
	}
	.footer-bottom__contacts.hide {
		display: block;
	}
	.footer-side {
		display: block;
		float: left;
		width: 250px;
		margin: 0 0 0 -100%;
		position: relative;
		padding-right: 50px;
	}
	.footer-side .copyright .nobr {
		display: block;
	}
}

/*FOOTER end*/
/*======================================================*/
/*MEDIA QUERIES*/
/*======================================================*/
/*======================================================*/
/*PRINT STYLES*/
/*======================================================*/
@media print {
	*,
	*:before,
	*:after {
		background: transparent !important;
		color: #000 !important;
		box-shadow: none !important;
		text-shadow: none !important;
	}
	a, a:visited {
		text-decoration: underline;
	}
	a[href^=http]:after {
		content: " <" attr(href) "> ";
	}
	a[href]:after {
		content: " (" attr(href) ")";
	}
	abbr[title]:after {
		content: " (" attr(title) ")";
	}
	a[href^="#"]:after, a[href^="javascript:"]:after {
		content: "";
	}
	pre, blockquote {
		border: 1px solid #999;
		page-break-inside: avoid;
	}
	thead {
		display: table-header-group;
	}
	tr, img {
		page-break-inside: avoid;
	}
	img {
		max-width: 100% !important;
	}
	p, h2, h3 {
		orphans: 3;
		widows: 3;
	}
	h2, h3 {
		page-break-after: avoid;
	}
	.sidebar,
	.aside,
	.print-hidden {
		display: none !important;
	}
	.content {
		margin-left: 0 !important;
		margin-right: 0 !important;
	}
	.logo {
		background: none !important;
	}
	.logo > * {
		display: none;
	}
	.logo > img {
		display: inline-block !important;
	}
}
