@import "vars";
@import "mixins";

/*search-form*/
.search-form {
	background-color: #fff;
	opacity: 0;
	visibility: hidden;
	transition-property: opacity, visibility;
	transition-duration: $animation-speed;
	z-index: 10;

	.form-is-open & {
		opacity: 1;
		visibility: visible; }

	.header & {
		position: absolute;
		left: 0;
		top: 30px;
		bottom: 0;
		width: 100%;
		transition-property: top;
		transition-duration: $animation-speed;

		.header-transform & {
			top: 0; }

		@media only screen and (max-width: $mq-desk-md) {
			top: 3px; }

		@media only screen and (max-width: $mq-desk-sm) {
			top: 0; } }

	.layout-inside {
		height: 100%; }

	form {
		display: table;
		width: 100%;
		height: 100%; }

	.input-wrapper {
		display: table-cell;
		vertical-align: middle;
		position: relative;
		padding: 0 30px;
		opacity: 0;
		//transform: scale(0.9, 0.9)
		//transition-property: transform
		//transition-duration: $animation-speed

		.form-is-open & {
			opacity: 1;
 } }			//transform: scale(1)

	input {
		&[type="search"], &[type="text"] {
			font-size: 20px;
			line-height: 30px;
			color: $text-color;
			padding: 0;
			height: auto;
			background: none;
			box-shadow: none;
			border: none;
			margin: 0; }

		$ph-color: #999;
		&::-webkit-input-placeholder {
			color: $ph-color !important; }

		&:-moz-placeholder {
			color: $ph-color !important; }

		&::-moz-placeholder {
			color: $ph-color !important; }

		&:-ms-input-placeholder {
			color: $ph-color !important; }

		&.placeholder {
			color: $ph-color !important; } }

	.btn-search {
		position: absolute;
		left: -14px;
		top: 50%;
		width: 40px;
		height: 40px;
		transform: translate(0, -50%);

		input[type="submit"] {
			padding: 0;
			border: none;
			background: none;
			margin: 0; } }

