.btn-search-close {
	position: absolute;
	right: -14px;
	top: 50%;
	width: 40px;
	height: 40px;
	transform: translate(0, -50%);

	span {
		@include hide-text; }

	i {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 10px;
		height: 10px;
		font-size: 0;
		line-height: 0;
		z-index: 2;
		pointer-events: none;
		transform: translate(-50%, -50%); }

	i:before, i:after {
		content: '';
		position: absolute;
		left: 50%;
		top: 50%;
		height: 2px;
		width: 12px;
		margin: -1px 0 0 -6px;
		border-radius: 2px;
		background-color: $text-color;
		transition-property: background-color;
		transition-duration: $animation-speed; }

	i:before {
		-webkit-transform: rotate(-45deg);
		-ms-transform: rotate(-45deg);
		transform: rotate(-45deg); }

	i:after {
		-webkit-transform: rotate(45deg);
		-ms-transform: rotate(45deg);
		transform: rotate(45deg); } }

/*search-form end*/

/*user form*/
.form-title {
	display: block;
	@include font-title;
	padding-bottom: $bottom-space;
	color: $text-color-alt;
	font-size: 2.4rem;
	line-height: 1.2;
	text-align: center; }

.user-form {
	background: #fff;
	//padding: 20px 30px
	//border: 1px solid $border-color
	margin-bottom: $bottom-space;

	.text-layout {
		margin-left: 0;
		margin-right: 0; }

	form {
		display: block; }

	textarea {
		height: 152px;
		max-height: 152px;
		resize: none; }

