@import "vars";
@import "mixins";

/*swiper slider*/
.swiper-button-next, .swiper-button-prev {
	position: absolute;
	top: 50%;
	width: 50px;
	height: 50px;
	margin-top: -25px;
	background: none;

	&.swiper-button--alt {
		border: 2px solid #000;
		border-radius: 50%;
		transition-property: border-color;
		transition-duration: $animation-speed;

		.no-touchevents &:hover {
			border-color: $text-color-alt; } }

	&.swiper-button-disabled {
		opacity: .35;
		cursor: auto;
		pointer-events: none; }

	span {
		@include hide-text; }

	i {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 10px;
		height: 16px;
		//background-color: chocolate
		z-index: 2;
		pointer-events: none;
		transform: translate(-50%, -50%);

		&:before, &:after {
			content: '';
			position: absolute;
			height: 12px;
			width: 2px;
			border-radius: 2px;
			backface-visibility: hidden;
			background-color: $text-color;
			transform-origin: center center;
			transition-property: background-color;
			transition-duration: 0.2s; } }

	.no-touchevents &:hover i {
		&:before, &:after {
			background-color: $text-color-alt; } } }

.swiper-button-prev {
	i {
		&:before, &:after {
			left: 0; }

		&:before {
			bottom: 50%;
			transform-origin: left bottom;
			transform: rotate(45deg); }

		&:after {
			top: 50%;
			transform-origin: left top;
			transform: rotate(-45deg); } } }

.swiper-button-next {
	i {
		&:before, &:after {
			right: 0; }

		&:before {
			bottom: 50%;
			transform-origin: right bottom;
			transform: rotate(-45deg); }

		&:after {
			top: 50%;
			transform-origin: right top;
			transform: rotate(45deg); } } }

.slider {
	&__toolbar {
		min-height: 44px;
		position: relative;
		margin-bottom: 1rem;

		.swiper-pagination {
			&-fraction {
				position: relative;
				bottom: auto;
				left: auto;
				width: auto;
				text-align: left;
				font-size: 1.4rem;
				line-height: 1.5;
				font-weight: bold;
				color: $text-color;
				text-transform: uppercase;
				letter-spacing: 0.1em;
				padding: 13px 0; }

			&-current {
				color: $text-color-alt; } }

		.swiper-button {

			&-prev {
				left: auto;
				right: 30px; }

			&-next {
				right: -20px; } }

		&--bottom {
			min-height: 44px;
			position: relative;

			.swiper-button {

				&-prev {
					left: -20px;

					&.swiper-button--alt {
						left: 0; } }

				&-next {
					right: auto;
					left: 30px;

					&.swiper-button--alt {
						left: 70px; } } } } } }
/*swiper slider end*/
